html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  min-width: 360px;
  margin: 0;
  padding: 0;
  font-family: "Gilroy", "Geneva", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: normal;
  color: #000000;
  background-color: #e0ddd7;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-header {
  background-color: #e0ddd7;
  position: fixed;
  z-index: 1;
}
.page-header__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #e0ddd7;
  position: fixed;
  z-index: 2;
  top: 0;
  width: 100%;
  padding: 0 15px 18px 15px;
}
.page-header__logo {
  align-self: flex-start;
  background-color: #ffffff;
  padding: 12px 15px 12px 15px;
  border-radius: 0 0 25px 25px;
  box-shadow: 0px 4px 12px -6px rgba(0, 0, 0, 0.5);
}
.page-header__link {
  display: block;
  position: relative;
  width: 290px;
  padding: 10px 65px 10px 80px;
  border-radius: 20px;
  box-shadow: 0px 4px 12px -6px rgba(0, 0, 0, 0.5);
  font-size: 18px;
  font-weight: bold;
  line-height: normal;
  text-decoration: none;
  color: #000000;
  background-color: #ffffff;
}
.page-header__link--close {
  display: none;
}
.page-header__link::before {
  content: "";
  position: absolute;
  top: 7px;
  left: 42px;
  width: 26px;
  height: 26px;
  background-image: url("../img/icons_buttons.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}
.page-header__wrapper::before {
  content: "";
  position: absolute;
  top: 181px;
  left: 0px;
  width: 100%;
  height: 12px;
  background-color: #e4dccc;
  box-shadow: inset 0px 3px 8px 2px #c2baaa;
}
.page-header--scroll::before {
  top: 85px;
}
.nav--open::before {
  display: none;
}
@media (max-width: 767px) {
  .page-header--scroll {
    padding-bottom: 0;
  }
  .page-header--scroll .page-header__logo {
    padding: 10px 8px 8px 8px;
  }
  .page-header--scroll .page-header__logo .logo-image {
    width: 140px;
    height: 50px;
  }
  .page-header--scroll .page-header__link {
    position: absolute;
    padding: 0;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    top: 15px;
    right: 80px;
  }
  .page-header--scroll .page-header__link span {
    display: none;
  }
  .page-header--scroll .page-header__link::before {
    top: 12px;
    left: 12px;
  }
  .page-header--scroll .main-nav__toggle {
    top: -55px;
  }
  .page-header--scroll .main-nav--opened .main-nav__toggle {
    top: -55px;
  }
  .page-header--scroll .site-list__item:last-child {
    margin-bottom: 20px;
  }
}
@media (min-width: 768px) {
  .page-header__wrapper {
    padding-bottom: 29px;
  }
  .page-header__logo {
    display: none;
  }
  .page-header__link {
    align-self: flex-end;
    margin-right: 100px;
  }
  .page-header__wrapper::before {
    top: 75px;
  }
}
@media (min-width: 1224px) {
  .page-header {
    width: 100%;
    height: 82px;
  }
  .page-header::before {
    content: "";
    position: absolute;
    top: 75px;
    left: 0px;
    width: 100%;
    height: 12px;
    background-color: #e4dccc;
    box-shadow: inset 0px 3px 8px 2px #c2baaa;
  }
  .page-header__wrapper {
    flex-direction: row;
    justify-content: space-between;
    padding: 0;
    max-width: 1600px;
  }
  .page-header__wrapper::before {
    display: none;
  }
  .page-header__link {
    align-self: center;
    margin-right: 15px;
    padding: 10px 14px 10px 44px;
  }
  .page-header__link::before {
    left: 15px;
  }
}
@media (min-width: 1600px) {
  .page-header__wrapper {
    left: 50%;
    margin-left: -800px;
  }
}
.main-nav {
  position: relative;
  padding-top: 20px;
  padding-bottom: 5px;
  width: 100%;
}
.main-nav--opened {
  padding-bottom: 0;
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.main-nav__toggle {
  position: absolute;
  top: -50px;
  right: 0;
  z-index: 2;
  display: block;
  width: 50px;
  height: 50px;
  background-color: inherit;
  cursor: pointer;
  border: none;
}
.main-nav--closed .site-list__item {
  display: none;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0px;
  right: 0px;
  width: 50px;
  height: 50px;
  border-radius: 25px;
  background-image: url("../img/menu.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}
.main-nav--closed .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 1px;
  right: 1px;
  width: 48px;
  height: 48px;
  border-radius: 25px;
  box-shadow: 0px 4px 10px -2px #be5b60;
  z-index: -1;
}
.main-nav--opened .main-nav__toggle {
  content: "";
  position: absolute;
  top: -50px;
  right: 0px;
  width: 50px;
  height: 50px;
}
.main-nav--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0px;
  right: 0px;
  width: 50px;
  height: 50px;
  background-image: url("../img/menu_clouse.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 1px;
  right: 1px;
  width: 48px;
  height: 48px;
  border-radius: 25px;
  box-shadow: 0px 4px 10px -2px #be5b60;
  z-index: -1;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__wrapper {
  position: static;
  min-height: 0;
}
.main-nav--nojs .site-list__item {
  display: block;
}
.main-nav--nojs .user-list__item {
  display: block;
}
@media (min-width: 768px) {
  .main-nav {
    padding-top: 10px;
  }
  .main-nav__toggle {
    top: 12px;
  }
  .main-nav--opened .main-nav__toggle {
    top: 12px;
  }
}
@media (min-width: 1224px) {
  .main-nav__toggle {
    display: none;
  }
  .main-nav--closed .site-list__item {
    display: block;
  }
  .main-nav {
    margin-left: 243px;
    margin-right: 10px;
    padding: 0;
  }
}
.site-list__item a {
  display: block;
  text-decoration: none;
  color: inherit;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 18px;
  line-height: normal;
}
.site-list__item:hover {
  background-color: #cc6666;
}
.site-list__item:hover a:hover {
  font-weight: bold;
  line-height: normal;
  color: #ffffff;
}
.site-list__item {
  cursor: pointer;
  width: 100%;
  text-align: center;
}
.site-list__item:first-child {
  margin-top: 20px;
}
.site-list {
  background-color: #e0ddd7;
}
.site-list__phone span {
  width: 180px;
  position: relative;
}
.site-list__phone span::before {
  content: "";
  position: absolute;
  top: -2px;
  left: -30px;
  width: 26px;
  height: 26px;
  background-image: url("../img/mobile_icon_black.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  z-index: 1;
}
@media (min-width: 768px) {
  .main-nav--opened .site-list {
    margin-top: 100px;
  }
}
@media (min-width: 1224px) {
  .site-list {
    display: flex;
    justify-content: space-between;
    max-width: 900px;
    margin: 0 auto;
  }
  .site-list__item--phone {
    display: none!important;
  }
  .site-list__item:first-child {
    margin-top: 0px;
  }
  .site-list__item a {
    padding: 27px 20px;
  }
  .site-list__item a:hover {
    padding: 26px 20px;
  }
}
.page-main {
  background-color: #e0ddd7;
  padding-top: 190px;
  position: relative;
  z-index: 0;
}
@media (min-width: 768px) {
  .page-main {
    padding-top: 85px;
  }
}
.page-main__logo {
  display: none;
}
@media (min-width: 768px) {
  .page-main__logo {
    display: block;
    overflow: hidden;
    position: fixed;
    top: 0;
    z-index: 9;
    margin-left: 15px;
  }
}
@media (min-width: 1224px) {
  .page-logo__wrapper {
    max-width: 1600px;
    margin: 0 auto;
  }
}
.page-main__title {
  font-size: 24px;
  font-weight: 900;
  text-align: center;
  padding-top: 120px;
  margin-top: -120px;
}
@media (min-width: 768px) {
  .page-main__title {
    font-size: 48px;
  }
}
